import Heading from './heading'
import Footer from './footer';

function Layout(props){
    return (
        <div>
          <Heading/>
          {props.children}
          <Footer/>
          {/* <script src="../static/bootstrap/jquery.js"></script> */}
          <script src="http://code.jquery.com/jquery-3.3.1.js"></script>
        </div>
      )
}
export default Layout